import React, {useRef} from 'react';
import styled from 'styled-components';
import IconButton from "./IconButton";
import Icon from "./Icon";

const Container = styled.div`
    box-sizing: border-box;
    flex: 0 0 100%;
    padding: 12px;
    user-select: none;
`;

const Base = styled.div`
    transition: all .5s;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    z-Index: 900;
    ${props => props.active ? `
        border-radius: 0;
        height: 100vh;
        position: fixed;
        bottom: 0;
        left: 0;` : ""}
`;

const Image = styled.div`
    flex-grow: 1;
    transition: height .5s ease;
    height: 200px;
    background-image: url('${props => props.image}');
    background-size: cover;
    background-position: center center;
`;

const Article = styled.div`
    padding: 18px;
`;

const Title = styled.div`
    padding-bottom: 18px;
    font-size: 14pt;
    font-weight: bold;
`;

const Text = styled.div`
    transition: .5s ease max-height;
    color: #666;
    font-size: 12pt;
    overflow: auto;
    max-height: 100px;
    
    ${props => props.active ? "max-height: 600px" : ""}
`;

const BottomBar = styled.div`
    padding: 8px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const Actions = styled.div`
    flex-grow: 1;
    display: flex;
    justify-content: space-evenly;
`;

const DateText = styled.div`
    flex-grow: 8;
`;

const Open = styled.div`
    color: white;
    float: right;
    padding: 8px;
`;

const Card = props => {
    const [big, setBig] = React.useState({open: false, x: null, y: null, width: null, height: null, animating: false});
    const body = useRef(null);
    const text = useRef(null);
    const img = useRef(null);

    let doubleTap = false;
    let last, path = 0, blocked = false;
    let animationFrame = 0;

    const handleTouch = event => {
        if(big.animating) return;
        if(big.open) {
            event.stopPropagation();
        }
        if(event.type === 'touchstart') {
            if (doubleTap === true) {
                if(big.open) {close()} else {open()}
            }
            doubleTap = true;

            last = event.touches[0].clientY;

            window.setTimeout(() => doubleTap = false, 200);
        }
        if((event.type === 'touchend' || 'touchcancel' === event.type)) {
            window.cancelAnimationFrame(animationFrame);
            blocked = false;
            if(!big.animating) {
                body.current.style.transition = '';
                body.current.style.bottom = '';
                path = 0;
            }
        }
        if(event.type === 'touchmove' && big.open) {
            if(text.current === event.target) return;

            body.current.style.transition = 'none';
            path += last - event.touches[0].clientY;
            last = event.touches[0].clientY;

            if(Math.abs(path) > window.innerHeight * 0.25) {
                body.current.style.transition = '';
                close(path);
            }

            if(blocked) return;

            animationFrame = window.requestAnimationFrame(() => {
                if(big.animating) return;
                body.current.style.bottom = path + 'px';
                blocked = false;
            });

            blocked = true;
        }
    };

    const close = () => {

        window.requestAnimationFrame(() => {
            body.current.style.transition = 'none';
            body.current.style.width = body.current.offsetWidth + 'px';
            body.current.style.height = body.current.offsetHeight + 'px';

            body.current.style.transition = '';

            body.current.style.left = big.x + 'px';
            body.current.style.bottom = big.y + 'px';
            body.current.style.width = big.width + 'px';
            body.current.style.height = big.height + 'px';
            body.current.style.position = 'fixed';

            setBig({...big, open: false, animating: true});

            window.setTimeout(() => {body.current.style = ''; setBig({...big, open:false, animating: false})}, 500);
        });
    };

    const open = event => {

        body.current.style.transition = 'none';

        body.current.style.left = 12 + 'px';
        body.current.style.bottom = window.innerHeight - body.current.offsetTop - body.current.offsetHeight + 'px';
        body.current.style.width = (body.current.offsetWidth) + 'px';
        body.current.style.height = (body.current.offsetHeight) + 'px';

        body.current.style.transition = '';

        setBig({...big,
            open: true,
            x: 12,
            y: window.innerHeight - body.current.offsetTop - body.current.offsetHeight,
            width: body.current.offsetWidth,
            height: body.current.offsetHeight,
        });

        body.current.style = '';
    };

    return (
        <Container>
            <Base ref={body} onTouchStart={handleTouch} onTouchMove={handleTouch} onTouchEnd={handleTouch} active={big.open}>
                <Image ref={img} active={big.open} image={props.image}>
                    <Open onTouchStart={open}>
                        <Icon color={big.open ? 'transparent' : 'white'} size={'24px'} style={{textShadow: "0 0 8px rgba(0,0,0,0.5)"}}>
                            fullscreen
                        </Icon>
                    </Open>
                </Image>
                <Article>
                    <Title>
                        {props.title}
                    </Title>
                    <Text ref={text} active={big.open}>
                        {props.children}
                    </Text>
                </Article>
                <BottomBar>
                    <DateText>
                        {props.date}
                    </DateText>
                    <Actions>
                        <IconButton activeColor={'red'}>thumb_down</IconButton>
                        <IconButton activeColor={'green'}>thumb_up</IconButton>
                        <IconButton>share</IconButton>
                    </Actions>
                </BottomBar>
            </Base>
        </Container>
    )
};

export default Card;
