import React, {useRef, useEffect} from 'react';
import styled from 'styled-components';
import {Indicator, IndicatorBase} from "./Components/Indicator";

const Base = styled.div`
    width: 100%;
    display: flex;
    overflow: hidden;
`;

const Container = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
`;

const Carousel = props => {
    const body = useRef(null);

    let last = null;
    let path = [0, 0];
    let animationFrame = 0;
    let indicators = [];
    let indicatorRefs = [];
    let index = 0;

    useEffect(() => {
        updateIndicators(0);
    });

    const setIndex = (i) => {
        if(i < 0) {
            index = body.current.childElementCount + i;
        } else {
            index = i % body.current.childElementCount;
        }
    };

    const moveToIndex = (i) => {
        let x0 = body.current.scrollLeft;
        setIndex(i);
        let x1 = body.current.offsetWidth * index;

        updateIndicators(index);
        window.cancelAnimationFrame(animationFrame);
        animate(x0, x1, new Date().getTime(), 300, body.current, "scrollLeft", "easeInOutCubic");
    };

    const slide = (event) => {
        body.current.scrollLeft += last[0] - event.touches[0].clientX;
    };

    for(let i = 0; i < props.children.length; i++) {
        indicators.push(
            <div style={{padding: 8}} onTouchStart={() => moveToIndex(i)} key={i} >
                <Indicator  ref={ref => indicatorRefs.push(ref)}/>
            </div>
            );
    }

    const handleTouch = event => {
        if(event.type === 'touchmove') {
            let touch = event.touches[0];

            if(last === null) last = [touch.clientX, touch.clientY];

            path[0] += touch.clientX - last[0];
            path[1] += touch.clientY - last[1];

            slide(event);

            last = [touch.clientX, touch.clientY];
        }
        if(event.type === 'touchend') {
            let direction = '';
            let magnitude = Math.sqrt(path[0] * path[0] + path[1] * path[1]);
            path[0] /= magnitude;
            path[1] /= magnitude;

            if(Math.abs(magnitude) > 50) {
                if (Math.abs(path[0]) > Math.abs(path[1])) {
                    if (path[0] > 0) {
                        direction = [-1, 0];
                    } else {
                        direction = [1, 0];
                    }
                } else {
                    if (path[1] > 0) {
                        direction = [0, 1];
                    } else {
                        direction = [0, -1];
                    }
                }
            } else {
                direction = [0, 0];
            }

            onSwipe(direction);

            last = null;
            path = [0, 0];
        }
    };

    const onSwipe = (direction) => {
        moveToIndex(index + direction[0]);
    };

    const updateIndicators = index => {
        for(let i = 0; i < indicatorRefs.length; i++) {
            if(i === index) {
                indicatorRefs[i].style.transform = "scale(2)";
                indicatorRefs[i].style.backgroundColor = "#9c4dcc";
                indicatorRefs[i].style.boxShadow = "0 0 4px 0 rgba(0, 0, 0, .5)";
            } else {
                indicatorRefs[i].style.transform = "";
                indicatorRefs[i].style.backgroundColor = "";
                indicatorRefs[i].style.boxShadow = "";
            }
        }
    };

    const animate = (x0, x1, start, duration, obj, property, easing = 'linear') => {
        const easingFunction = {
            linear: t => {return t},
            easeInQuad: t => {return t*t},
            easeOutQuad: t => {return t*(2-t) * t},
            easeInOutQuad: t => {return t < .5 ? 2 * t * t : -1 + (4 - 2 * t) * t},
            easeInCubic: t => {return t * t * t},
            easeOutCubic: t => {return (--t)*t*t+1},
            easeInOutCubic: t => { return t<.5 ? 4*t*t*t : (t-1)*(2*t-2)*(2*t-2)+1 },
            easeInQuart: t => { return t*t*t*t },
            easeOutQuart: t => { return 1-(--t)*t*t*t },
            easeInOutQuart: t => { return t<.5 ? 8*t*t*t*t : 1-8*(--t)*t*t*t },
            easeInQuint: t => { return t*t*t*t*t },
            easeOutQuint: t => { return 1+(--t)*t*t*t*t },
            easeInOutQuint: t => { return t<.5 ? 16*t*t*t*t*t : 1+16*(--t)*t*t*t*t }
        };

        animationFrame = window.requestAnimationFrame(() => {
            let d = x1 - x0;

            if(d === 0) return;

            let t = (new Date().getTime() - start) / duration;

            if(t > 1) {
                obj[property] = x1;
                return;
            }

            obj[property] = x0 + d * easingFunction[easing](t);
            animate(x0, x1, start, duration, obj, property, easing);
        });
    };

    return (
        <Container>
            <Base
                ref = {body}
                onTouchMove={handleTouch}
                onTouchStart={handleTouch}
                onTouchEnd={handleTouch}>
                {props.children}
            </Base>
            <IndicatorBase>
                {indicators}
            </IndicatorBase>
        </Container>
    )
};

export default Carousel;
